* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: none;
}

body {
  background: #efefef;
}

header {
  width: 100%;
  background: #333;
}
header .inner {
  width: 1180px;
  height: 100px;
  margin: 0px auto;
  position: relative;
}
header .inner h1 {
  position: absolute;
  left: 0px;
  bottom: 20px;
}
header .inner h1 a {
  font: bold 24px/1 "arial";
  color: #fff;
}
header .inner #gnb {
  position: absolute;
  bottom: 0px;
  right: 0px;
}
header .inner #gnb li {
  float: left;
}
header .inner #gnb li a {
  display: inline-block;
  padding: 22px 30px;
  font: 14px/1 "arial";
  color: #fff;
}

footer {
  width: 100%;
  border-top: 1px solid #ddd;
  padding: 50px 0px;
}
footer .inner {
  width: 1180px;
  margin: 0px auto;
  text-align: center;
}
footer .inner p {
  font: 12px/1 "arial";
  color: #888;
  text-align: center;
}

.content {
  padding: 150px 0px;
}
.content > .inner {
  width: 1180px;
  margin: 0px auto;
  min-height: 500px;
}
.content > .inner h1 {
  font: normal 30px/1 "arial";
  color: #555;
  margin-bottom: 30px;
}

@media screen and (max-width: 1179px) {
  .content > .inner {
    width: 100%;
    padding: 0px 40px;
  }
}
.board .inner article {
  width: 100%;
  padding: 20px;
  border: 1px solid #bbb;
  margin-bottom: 20px;
}

.gallery .inner {
  position: relative;
}
.gallery .inner .loading {
  position: absolute;
  top: 0px;
  left: 50%;
  width: 50%;
  transform: translateX(-50%);
}
.gallery .inner .list {
  opacity: 0;
  transform: translateY(200px);
  transition: 1s;
  position: relative;
}
.gallery .inner .list.on {
  opacity: 1;
  transform: translateY(0px);
}
.gallery .inner .list ul {
  width: 100%;
}
.gallery .inner .list ul .item {
  width: 25%;
  padding: 10px;
  float: left;
}
.gallery .inner .list ul .item .inner {
  width: 100%;
  padding: 12px;
  background: #fff;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.1);
}
.gallery .inner .list ul .item .inner img {
  width: 100%;
  margin-bottom: 10px;
}
.gallery .inner .list ul .item .inner h2 {
  font: normal 12px/1.2 "arial";
  color: #777;
  margin-bottom: 20px;
}

@media screen and (max-width: 1179px) {
  .gallery .item {
    width: 33.333% !important;
  }
}
@media screen and (max-width: 739px) {
  .gallery .item {
    width: 50% !important;
  }
}
@media screen and (max-width: 479px) {
  .gallery .item {
    width: 100% !important;
  }
}

